.type-search {

    .styled-form {
        // reset styled form settings
        font-family: inherit;
        font-weight: inherit;
        font-size: inherit;
        line-height: inherit;
        color: inherit;
        background: inherit;
    }

    .show-more {
        margin-top: $default-margin-small;
    }

    .search-results-header {
        @include clearfix;
        margin-bottom: $default-margin;
    }

    .search-query,
    .search-sort,
    .search-result {
        margin-bottom: $default-margin;
    }

    .search-input,
    .search-sort-options {
        margin-bottom: $default-margin;
    }

    .search-results-count {
        font-weight: $font-weight-bold;
    }

    .type-list-filter .filterbox {
        @include list-filter-box;
        > div {
            padding: $grid-gutter-width-half;
        }

        &:last-of-type {
           margin-bottom: $default-margin; // for lists this is 0, but in the search we want the margin always
        }
    }

    .search-result-heading {
        @extend .h4;
        margin-bottom: $default-margin-verysmall;

        .result-title {
            margin-right: $default-margin-verysmall;
            vertical-align: middle;
        }

        a:hover .search-badge {
            text-decoration: none;
        }

        + .search-result-date {
            margin-top: -1 * $default-margin-verysmall;
            margin-bottom: $default-margin-verysmall;
            color: $search-datelastmod-color;
        }
    }

    .search-badge {
        font-weight: $font-weight-base;
        font-family: $font-family-base;
        font-size: 50%;
        padding: $default-margin-extremesmall $default-margin-verysmall;
        @include round-corners($search-badge-border-radius);
        color: $search-badge-color;
        @if ($search-badge-background != transparent) {
            background: $search-badge-background;
        }
        border: $search-badge-border;
        white-space: nowrap;
        display: inline-block;
        vertical-align: middle;
    }

    @include media-breakpoint-up(lg) {

        .search-query,
        .search-sort,
        .search-results {
            &.has-facets {
                width: get-col-float-size(8);
                float: left;
            }
        }

        .search-facets {
            width: get-col-float-size(4);
            float: right;
        }

        .search-results-head {
            float: left;
            margin-bottom: 0;
        }

        .search-results-count {
            float: right;
        }

        .search-result-heading {
            .result-title {
                margin-right: $default-margin-quitesmall;
            }
        }

        .search-badge {
            font-size: 60%;
            padding: $default-margin-extremesmall $default-margin-quitesmall;
        }
    }
}
